body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}
html,
body,
h1,
h2,
h3,
h4,
h5,
h6,
ul {
  margin: 0;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root {
  --primary: #410e61;
  --primary-light: rgba(65, 14, 97, 0.3);
  --title: #1d062a;
  --personal: #166a8e;
  --personal-light: #e2eef3;
  --uncategorized: #410e61;
  --uncategorized-light: #e1d8e9;
  --study: #2e8e16;
  --study-light: #d8e5d5;
  --work: #c88b15;
  --work-light: #f6edd8;
  --todo: #8e1d16;
  --todo-light: #f3d6d4;
  --gradient: linear-gradient(
    180deg,
    rgba(238, 170, 194, 0.5) 0%,
    rgba(65, 14, 97, 0.5) 100%
  );
  --light: #444444;
}
.DraftEditor-root {
  background-color: white;
  padding: 10px;
  box-sizing: border-box;
}
